import React from 'react';
import classes from './Image_wth_txt.module.css';


const Image_wth_txt = ({children, ...props}) => {
    return(
        <div className={classes.image_wth_txt_div}>
                <img {...props} className={classes.image_wth_txt}></img>
            <div className={classes.image_wth_txt_txt}>
                {children}
            </div> 
        </div>
        
    )
}

export default Image_wth_txt;