import React from 'react';
import classes from './HomePage.module.css';
import ImageRound from "../components/UI/image_wth_txt/ImageRound"
import image_round from  "../images/myPhoto.jpg";


function HomePage() {
  return (
    <div className={classes.home_page}>
        <div className={classes.home_page_btn_my_proj}>
          <div className={classes.home_page_right_space}>
            <div className={classes.home_page_right_space1}>
              <div className={classes.home_page_txt_position}>
                Place for text is here
              </div>
            </div>
          </div>
        </div>
        <div className={classes.home_page_img}>
        <ImageRound src={image_round} alt={"img"}/>
          <div className={classes.home_page_txt}>
            Vladislav Zalegay 
          </div>
        </div>

    </div>
  );
}

export default HomePage;