import React from 'react';
import './App.css';
import {BrowserRouter, Routes, Route, Link} from "react-router-dom";
import { useNavigate } from 'react-router';
import MyProjects from "./pages/MyProjects";
import HomePage from "./pages/HomePage";
import Page404 from "./pages/Page404";
import MyCV from "./pages/MyCV";

function App() {

  window.onload = function set_active_link(){
    const currentRoute = window.location.pathname;
    if (currentRoute === "/Home" || currentRoute === "/")
    {
      document.getElementById('link_home').className = 'link_active';
    }
    else if (currentRoute === "/MyProjects")
    {
      document.getElementById('link_my_project').className = 'link_active';
    }
    else if (currentRoute === "/MyCV")
    {
      document.getElementById('link_my_cv').className = 'link_active';
    }
  }




  function set_link_my_project() {
    document.getElementById('link_my_project').className = 'link_active';
    document.getElementById('link_home').className = 'link';
    document.getElementById('link_my_cv').className = 'link';
  }

  function set_link_home() {
    document.getElementById('link_my_project').className = 'link';
    document.getElementById('link_home').className = 'link_active';
    document.getElementById('link_my_cv').className = 'link';
  }
  
  function set_link_my_cv() {
    document.getElementById('link_my_project').className = 'link';
    document.getElementById('link_home').className = 'link';
    document.getElementById('link_my_cv').className = 'link_active';
  }

  return (

    <div className='App'>
      <BrowserRouter>
        <div className="navbar_bg">
          <div className="navbar">
            <div className="navbar_links">
              <Link className="link" id="link_home" to="/myReactProjectCV/Home" onClick={set_link_home}>Home</Link>
              <Link className="link" id="link_my_project" to="/myReactProjectCV/MyProjects" onClick={set_link_my_project}>My projects</Link>
              <Link className="link" id="link_my_cv" to="/myReactProjectCV/MyCV" onClick={set_link_my_cv}>My CV</Link>
              <a className="link_exit" href="https://by.linkedin.com/in/vladislav-zalehay-7725431b5">Exit</a>            
            </div>
          </div>
        </div>
        <Routes>
          <Route path="/myReactProjectCV/MyProjects" element={<MyProjects/>}/>
          <Route path="/myReactProjectCV/Home" element={<HomePage/>}/>
          <Route path="/myReactProjectCV/" element={<HomePage/>}/>
          <Route path="/myReactProjectCV/MyCV" element={<MyCV/>}/>
          <Route path="*" element={<Page404/>}/>
        </Routes>
      </BrowserRouter>
    </div>
  );
  
}

export default App;
