import React from 'react';
import classes from './MyCV.module.css';
import image_main from  "../images/myPhoto.jpg";
import Header_txt from  "../components/UI/txt_wth_header/Txt_header";
import Txt_space from  "../components/UI/txt_wth_header/Txt_space";

function MyCV() {
  return (
    <div className={classes.right_space}>
      <div className={classes.left_space}>
        <img src={image_main} alt={"img"} className={classes.image_main}></img>
          <div className={classes.name_space}>
            Vladislav Zalegay
          </div>
      
        
        <Txt_space>
          <Header_txt>
           Location
          </Header_txt>
            Belarus, Minsk
        </Txt_space>

        <Txt_space>
          <Header_txt>
          Contacts
          </Header_txt>
            <a href="https://www.linkedin.com/in/vladislav-zalehay-7725431b5" class="global">LinkedIn</a>
            <br/>
            E-mail: vlad.zalehay@mail.ru
        </Txt_space>

        <Txt_space>
          <Header_txt>
          Education
          </Header_txt>
            BSUIR,
            Radio engineering (Programmable radio electronic means)
        </Txt_space>

        <Txt_space>
          <Header_txt>
          Skills
          </Header_txt>
            C/C++<br/>
            JavaScript<br/> 
            Python 
        </Txt_space>

        <Txt_space>
          <Header_txt>
          Language
          </Header_txt>
            Russian: Native<br/>
            English: A2-B1
        </Txt_space>
      </div>
    </div>
  );
}

export default MyCV;